import { useState, useEffect } from "react";

import './CompSlider.css';

export default function CompCountDown() {
    const [inx, setInx] = useState(0);
    const [isDisabledPrev, setIsDisabledPrev] = useState(true);    
    const [isDisabledNext, setIsDisabledNext] = useState(true);        

    const json = {
        "slides": [
            "Slide 1",
            "Slide 2",
            "Slide 3",            
            "Slide 4",                        
            "Slide 5",                        
            "Slide 6",                        
            "Slide 7",                                                            
        ]             
    }    
    const data = json.slides;
    const chuck = [];
    while(data.length) {
        chuck.push(data.splice(0, 3));
    }

    useEffect(() => {
        if(chuck.length < 1 || inx >= chuck.length - 1) {
            setIsDisabledNext(true);
        } else {
            setIsDisabledNext(false);            
        }

        if(inx <= 0) {
            setIsDisabledPrev(true);
        } else {
            setIsDisabledPrev(false);            
        }        
    }, [inx, chuck.length]);

    const Slide = ( {value} : {value: string} ) => <span className="slide">({value})</span>;

    const handlePrev = () => {
        setInx((prev) => prev - 1);
    }

    const handleNext = () => {
        setInx((prev) => prev + 1);        
    }    

    return(
        <div className="container-slider">
            <div className="menu">
                <button className="ui left labeled icon button" onClick={handlePrev} disabled={isDisabledPrev}>
                    <i className="left arrow icon"></i> Prev
                </button>
                <button className="ui right labeled icon button" onClick={handleNext} disabled={isDisabledNext}>
                    <i className="right arrow icon"></i> Next
                </button>
            </div>
            <div className="slides">
                {chuck[inx].map((slide, i) => (
                    <Slide key={i} value={slide} />
                ))}
            </div>
        </div>
    )
}

