import React from "react";

// -------------------------------------------------------------------------------------
// Section
// -------------------------------------------------------------------------------------
type Props = {
  children?:
    | React.ReactChild
    | React.ReactChildren
    | React.ReactChild[]
    | React.ReactChildren[];
};

const Section = ({ children }: Props) => {
  return <div className="section">{children}</div>;
};
export { Section };

// -------------------------------------------------------------------------------------
// Row
// -------------------------------------------------------------------------------------
const Row = ({ children }: Props) => {
  return <div className="row">{children}</div>;
};
export { Row };

// -------------------------------------------------------------------------------------
// Col
// -------------------------------------------------------------------------------------
const Col2 = ({ children }: Props) => {
  return <div className="col-2">{children}</div>;
};
export { Col2 };

const Col3 = ({ children }: Props) => {
  return <div className="col-3">{children}</div>;
};
export { Col3 };

const Col4 = ({ children }: Props) => {
  return <div className="col-4">{children}</div>;
};
export { Col4 };

const Col5 = ({ children }: Props) => {
  return <div className="col-5">{children}</div>;
};
export { Col5 };

const Grid = {
  Section: Section,
};

export default Grid;
