import React, { useState } from "react";
import ReactDOM from "react-dom";

// External
import faker from "faker";

// Comments
import CommentDetail from "./comments/CommentDetail";

// Components
import CountdownClock from "./components/CountdownClock";
import Thermometer from "./components/Thermometer";
import CompSlider from "./components/CompSlider";

// Controls
import ApprovalCard from "./controls/ApprovalCard";

// Navigation
import Navigation from "./navigations/Navigation";

// Grid
import { Section } from "./grid/Grid";
import Segment from "./grid/Segment";
import Group3 from "./grid/Group3";

// Others
import Externals from "./others/external";

// CSS
import "./css/utilities.css";
import "./index.css";

const App = () => {
  const [curInx, setCurInx] = useState(0);

  const handleMenuUpdate = (inx: number) => {
    setCurInx(inx);
  };

  const menus: string[] = [
    "Navigation",
    "Countdown Clock",
    "Thermometer",
    "Image Slider",
    "Gird",
    "Segment",
    "Group3",
    "CommentDetail",
    "ApprovalCard",
    "Externals",
  ];
  const contents: JSX.Element[] = [
    <Navigation
      names={["menu 1", "menu 2", "menu 3"]}
      active_inx={0}
      onMenuUpdate={handleMenuUpdate}
    />,
    <CountdownClock />,
    <Thermometer current={15000} goal={20000} />,
    <CompSlider />,
    <Section>This is a test</Section>,
    <div className="contents-wrapper">
      <Segment>This is some contents</Segment>
      <Segment>Some other contents</Segment>
    </div>,
    <Group3 />,
    <div className="ui container comments">
      <CommentDetail
        author="Sam"
        timeAgo="Today at 4:45PM"
        content="Nice blog post 1!"
        avatar={faker.image.avatar()}
      />
      <CommentDetail
        author="Alex"
        timeAgo="Today at 2:00AM"
        content="I like the subject"
        avatar={faker.image.avatar()}
      />
      <CommentDetail
        author="Jane"
        timeAgo="Yesterday at 5:00PM"
        content="I like the writing"
        avatar={faker.image.avatar()}
      />
    </div>,
    <div className="ui container comments">
      <ApprovalCard>
        <div>
          <h4>Waring!</h4>
          Are you sure you want to do this?
        </div>
      </ApprovalCard>
      <ApprovalCard>
        <CommentDetail
          author="Sam"
          timeAgo="Today at 4:45PM"
          content="Nice blog post 1!"
          avatar={faker.image.avatar()}
        />
      </ApprovalCard>
    </div>,
    <Externals />,
  ];

  return (
    <div className="main-container">
      <div className="main-wrapper">
        <div className="container-menu">
          <Navigation
            names={menus}
            active_inx={curInx}
            onMenuUpdate={handleMenuUpdate}
          />
        </div>
        <div className="container-contents">
          <div className="contents-wrapper">{contents[curInx]}</div>
        </div>
      </div>
    </div>
  );
};

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.querySelector("#root")
);

// Autorefresh index.js
// npm install --save-dev @types/webpack-env
if (module.hot) {
  module.hot.accept();
}
