import "./Navigation.css";

type Props = {
  names: string[];
  active_inx: number;
  onMenuUpdate: (inx: number) => void;
  optional?: string;
};

const Navigation = ({ names, active_inx, onMenuUpdate }: Props) => {
  const handleClick = (inx: number) => {
    onMenuUpdate(inx);
  };

  return (
    <nav>
      {names.map((v, i) => (
        <li
          key={i}
          className={i === active_inx ? "active" : ""}
          onClick={() => {
            handleClick(i);
          }}
        >
          {v}
        </li>
      ))}
    </nav>
  );
};
export default Navigation;
