import { useState } from "react";

export default function Group3() {
  const [inx] = useState(0);
  const json = {
    contents: [
      "Slide 1",
      "Slide 2",
      "Slide 3",
      "Slide 4",
      "Slide 5",
      "Slide 6",
      "Slide 7",
    ],
  };
  const data = json.contents;
  const chuck = [];
  while (data.length) {
    chuck.push(data.splice(0, 3));
  }

  const Slide = ({ value }: { value: string }) => (
    <span className="slide">({value})</span>
  );

  return (
    <div className="container-slider">
      <div className="slides">
        {chuck[inx].map((slide, i) => (
          <Slide key={i} value={slide} />
        ))}
      </div>
    </div>
  );
}

